import React from 'react';
import {Tooltip,Divider} from 'antd';
import styles from './index.scss';
import iconfont from '../theme/iconfont.scss';
import {Toolbar,Command} from 'gg-editor';

class FlowToolbar extends React.Component{
    render(){
        return(
          <Toolbar style={styles.toolbar}>
              <Command name='undo'>
                  <Tooltip title='' placement='left' overlayClassName={styles.tooltip}>
                      <i className={`${iconfont.iconfont} ${iconfont.iconUndo}`}/>
                  </Tooltip>
              </Command>
              <Divider type='vertical'/>
          </Toolbar>
        );
    }
}

export default FlowToolbar;